import React from "react";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import { BrowserRouter , Switch, Route, Link, Redirect } from "react-router-dom";
import './App.css'

const AnimationRoute = () => (
  <BrowserRouter>
    <Route
      render={({ location }) => (
        <div style={styles.fill}>
          <Route
            exact
            path="/"
            render={() => <Redirect to="/hsl/10/90/50" />} // /hsl/10/90/50
          />

          <ul style={styles.nav}>
            <NavLink to="/hsl/10/90/50" id="red"></NavLink>
            <NavLink to="/hsl/120/100/40" id="green"></NavLink>
            <NavLink to="/rgb/33/150/243" id="blue"></NavLink>
            <NavLink to="/rgb/240/98/146" id="pink"></NavLink>
          </ul>
          <div style={styles.content}>
            {/**/}
            {/*css*/}
            <TransitionGroup>
              <CSSTransition
                // keyreact
                // demo location.key 
                key={location.pathname}
                // classNames css'classNames''s'
                classNames="fade"
                // 800mscss
                timeout={800}
              >
                <Switch location={location}>
                  <Route exact path="/hsl/:h/:s/:l" component={HSL} />
                  <Route exact path="/rgb/:r/:g/:b" component={RGB} />
                  <Route render={() => <div>Not Found</div>} />
                </Switch>
              </CSSTransition>
            </TransitionGroup>
          </div>
        </div>
      )}
    />
  </BrowserRouter>
);

const NavLink = props =>{ //NavLink
  // alert(JSON.stringify(props))
 return  (
  <li style={styles.navItem} className={props.id}>
    <Link {...props} style={{textDecoration:"none" }} className={props ? styles.actives:""} />
  </li>
);
}


// match : params
const HSL = ({ match: { params } }) => (
  <div
    style={{
      ...styles.hsl,
      background: `hsl(${params.h}, ${params.s}%, ${params.l}%)`
    }}
  >
    hsl({params.h}, {params.s}%, {params.l}%)
  </div>
);




/**
 * this ways:
 * 1
 * **/
class RGB extends React.Component{
  constructor(props){
    super(props)
    this.state={
      datas:props
    }
  }
componentDidMount(){
  const data = JSON.stringify(this.state.datas);
  console.log(data.match.url)
}
  render(){
    return <div
            style={{
              ...styles.rgb,
              background: `rgb(${this.props.match.params.r}, ${this.props.match.params.g}, ${this.props.match.params.b})`
            }}
          >
            rgb({this.props.match.params.r}, {this.props.match.params.g}, {this.props.match.params.b})
          </div>
  }
}
